let commonUtil = {
    waitLoading(){
      $('body').append('<div id="bg"><img src="/images/icons/loading.gif"></div>')
    },
    endLoading(){
      $('#bg').remove();
    },
    initwx(){
      var url = window.location.href.split('#')[0];
      $.ajax({
        url: 'https://pc.hboss.com/weixin/config/jsConfig',
        async: false,
        data: {
          url: url
        },
        success: function(res) {
          var data = res.data;
          wx.config({
            debug: false, // ,apialertpclogpc
            appId: data.appId, // 
            timestamp: data.timestamp, // 
            nonceStr: data.nonceStr, // 
            signature: data.signature, // 1
            jsApiList: [
              'onMenuShareTimeline',
              'onMenuShareAppMessage'
            ] // JSJS2
          });
          wx.error(function(res) {
            // configerrorconfigdebugresSPA
            history.go(0)
          });
        }
      })
    },
    jobListwx(){
      wx.ready(function() {
        // configreadyconfigconfigreadyready
        wx.onMenuShareTimeline({
          title: '', // 
          link: window.location.href, // JS
          imgUrl: 'http://ofot6413t.bkt.clouddn.com/3f04c2bf513091dc34ee46f01e8eee43.jpg', // 
          success: function() {
            // 
          },
          cancel: function() {
            // 
          }
        })
        wx.onMenuShareAppMessage({
          title: '', // 
          desc: '', // 
          link: window.location.href, // JS
          imgUrl: 'http://ofot6413t.bkt.clouddn.com/3f04c2bf513091dc34ee46f01e8eee43.jpg', // 
          type: '', // ,musicvideolinklink
          dataUrl: '', // typemusicvideo
          success: function() {
            // 
          },
          cancel: function() {
            // 
          }
        })
      })
    }
};


export default commonUtil;



